body {
	background-color: #c4b7a6; width: 100%;
}
#prior {
	position: relative; min-height: 1700px;
}
#board {
	width: 50%; width: 100%; min-width: 900px;
}
#main-title {
	font-family: "Comic Sans MS", cursive, sans-serif; font-weight: bold; font-size: 20pt; text-align: center; width: 350px; margin-left: auto; margin-right: auto; margin-bottom: 20px;
}
#userInfo, #boardInfo, #loading {
	width: 50%; margin: auto; background-color: darkgray; padding: 2%; border: 1px solid black; border-radius: 10px; font-family: "Comic Sans MS", cursive, sans-serif;
}
#container {
	display: grid; grid-template-columns: 50% 50%; color: black; height: 600px; 
	grid-gap: 1px; margin: 20px; font-family: "Comic Sans MS", cursive, sans-serif;
	border: 1px solid black; background-color: black; padding: 1px;
}
#action {
	height: 50px; font-family: "Comic Sans MS", cursive, sans-serif; font-weight: bold; font-size: 16pt; display: flex; width: 300px; margin: auto; align-items: center; justify-content: center;
}
#description {
	font-family: "Gill Sans", sans-serif; text-align: justify; line-height: 1.5em; min-width: 300px; margin-left: auto; margin-right: auto; padding: 30px; width: 50%
}
.tile {
	text-align: center; width: 100%; height: 300px; background-color: #dac292
}
#container .title {
	font-size: 16pt; background-color: grey; font-weight: bold;
}
ul {
	text-align: left; width: 90%; height: 320px; overflow-y: auto; list-style-type: "\1F44D"; list-style-position: inside; padding-left: 20px;
}
li {
	width: 450px; word-wrap: break-word; font-size: 15pt; padding-left: 10px;
}
#taskBoard {
	width: 100px
}
.modal {
	position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%) scale(0);
	border: 1px solid black; border-radius: 10px; z-index: 10; background-color: white;
	width: 500px; max-width: 80%; background-color: #c4b7a6; font-family: "Comic Sans MS", cursive, sans-serif; /*transition: 300ms ease-in-out;*/ 
}
.modal.active {
	transform: translate(-50%, -50%) scale(1);
}
.modal-header {
	padding: 10px 15px; display: flex; justify-content: space-between; align-items: center;
	border-bottom: 1px solid black;
}
.modal-header .title {
	font-size: 1.25rem; font-weight: bold;

}
.modal-header .close-button {
	border: none; outline: none; background: none; font-size: 1.25rem;
	font-weight: bold;
}
.modal-header {
	cursor: all-scroll; 
}
.close-button {
	cursor: pointer; 
}
.modal-body {
	padding: 10px 15px; font-weight: bold;
}
#overlay {
	position: fixed; top: 0; right: 0; left: 0; bottom: 0; opacity: 0;
	background-color: rgba(0,0,0,.5); pointer-events: none;
	/*transition: 300ms ease-in-out;*/
}
#overlay.active {
	opacity: 1;
	pointer-events: all;
}
.actionitem{
	display: flex; justify-content: space-between; border-radius: 10px;  margin-top: 10px;
}
.green {
	background-color: green;  
}
.red {
	background-color: red; 
}
.blue {
	background-color: grey;
}
button {
	background-color: #b9936c; border-radius: 10px; font-family: "Comic Sans MS", cursive, sans-serif; font-weight: bold;
}
button:hover {
	background-color: brown;
}
#submit {
	margin-top: 20px; height: 30px; font-weight: bold;
}
input[type='checkbox'] {
	height: 20px; width: 20px;
}
footer {
	position: absolute; bottom: 0; width: 100%; text-align: center; height: 2.5em;
}


